img {
  max-width: 100%;
  height: auto;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.page {
  height: 100%;
}

.page-body {
  min-width: 320px;
  font-family: "PT Sans Narrow", sans-serif;
  font-weight: 400;
  line-height: 14px;
  line-height: 26px;
  text-transform: uppercase;
  color: #000000;
  min-height: 100%;
  display: grid;
  grid-template-rows: min-content 1fr min-content;
  align-content: start;
  margin: 0;
  background-color: #F2F2F2;
}

@media (min-width: 1024px) {
  .page-wrapper {
    width: 1200px;
    margin: 0 auto;
    background-color: #fff;
    box-shadow: 0px 5px 15px 0px rgba(0, 1, 1, 0.2);
  }
}

.page-header {
  /* display: flex; */
}

.page-header__logo {
  width: 101px;
  height: 84px;
}

@media (min-width: 1024px) {
  .page-header__logo {
    width: 186px;
    height: 97px;
  }

  .page-header {
    border: 2px solid red;
  }
}
.main-nav__wrapper {
  background-color: #81B3D2;
}

.main-nav__list {
  display: flex;
  list-style: none;
  margin: 0;
  padding: 0;
}

.main-nav__list a {
  font-size: 18px;
  font-weight: 700;
  line-height: 40px;
  text-decoration: none;
  color: #ffffff;
}

@media (min-width: 768px) {
  .main-nav__toggle {
    display: none;
  }

  .main-nav__wrapper {
    width: 768px;
    margin: 0 auto;
    background-color: #ffffff;
  }

  .main-nav__list a {
    color: #000000;
  }
}
@media (min-width: 1024px) {
  .main-nav__wrapper {
    width: 1160px;
    margin: 0 auto;
  }
}
.container {
  padding: 0 20px;
}

.advantages__header {
  text-align: center;
}
.advantages__title, .advantages__intro {
  margin: 0;
  padding: 0;
}
.advantages__list {
  list-style-type: none;
  margin: 0;
  padding: 0;
}
.advantages__list--background {
  background-color: #EEEEEE;
}
.advantages h4, .advantages p {
  text-align: center;
  margin: 0;
  padding: 0;
}

.sity-item__box {
  background-color: #81B3D2;
  color: #ffffff;
}

.sity-advantages__list {
  list-style: none;
  margin: 0;
  padding: 0;
}

.sity-list__title, .sity-list__p {
  margin: 0;
  padding: 0;
}
.sity-list__icon {
  width: 39px;
  height: 37px;
}

@media (min-width: 768px) {
  .container {
    padding: 0 25px;
  }

  .advantages__header {
    width: 520px;
    margin: 0 auto;
  }

  .sity-item {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
  }

  .sity-advantages {
    grid-column: 1/3;
  }

  .sity-list {
    display: flex;
    text-align: center;
  }

  .test {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-template-rows: repeat(4, 1fr);
  }

  .sity-item__box {
    height: 256px;
    grid-column: 1/2;
    grid-row: 1/2;
  }

  .sity-item__img {
    height: 256px;
    grid-column: 2/4;
    grid-row: 1/2;
  }

  .sity-list {
    grid-column: 1/-1;
    grid-row: 2/3;
  }

  .bridge-item__img {
    grid-column: 1/3;
    grid-row: 3/4;
  }

  .bridge-item__box {
    grid-column: 3/4;
    grid-row: 3/4;
  }
}
.page-footer__hashtag {
  margin: 0;
}

.social__list {
  list-style: none;
  margin: 0;
  padding: 0;
}

.copyright__title {
  margin: 0;
}

@media (min-width: 768px) {
  .page-footer__wrapper {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 20px;
    justify-items: center;
  }
}